// pages/Settings.jsx
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import useTechnologiesApi from '../hooks/useTechnologiesApi';
import DataExporter from '../components/DataExporter';
import DataImporter from '../components/DataImporter';
import './Settings.css';

function Settings() {
  const { 
    technologies, 
    markAllCompleted, 
    resetAllStatuses,
    addTechnology,
    removeAllTechnologies
  } = useTechnologiesApi();

  const [showResetConfirm, setShowResetConfirm] = useState(false);
  const [showDeleteAllConfirm, setShowDeleteAllConfirm] = useState(false);

  //    
  const handleImport = (importedTechnologies) => {
    importedTechnologies.forEach(tech => {
      addTechnology(tech);
    });
  };

  //     
  const handleDeleteAll = () => {
    removeAllTechnologies();
    setShowDeleteAllConfirm(false);
  };

  const handleMarkAllCompleted = () => {
    markAllCompleted();
  };

  const handleResetAllStatuses = () => {
    resetAllStatuses();
    setShowResetConfirm(false);
  };

  const getCategoryStats = () => {
    const stats = {};
    technologies.forEach(tech => {
      if (!stats[tech.category]) {
        stats[tech.category] = 0;
      }
      stats[tech.category]++;
    });
    return stats;
  };

  const categoryStats = getCategoryStats();

  //      
  const getCategoryName = (category) => {
    const categoryNames = {
      'frontend': 'Frontend',
      'backend': 'Backend', 
      'database': ' ',
      'devops': 'DevOps',
      'mobile': ' ',
      'ai-ml': 'AI/ML',
      'cloud': ' ',
      'tools': '',
      'language': ' ',
      'other': ''
    };
    return categoryNames[category] || category;
  };

  //     
  const getCategoryIcon = (category) => {
    const icons = {
      'frontend': '',
      'backend': '',
      'database': '',
      'devops': '',
      'mobile': '',
      'ai-ml': '',
      'cloud': '',
      'tools': '',
      'language': '',
      'other': ''
    };
    return icons[category] || '';
  };

  //      ( )
  const sortedCategories = Object.entries(categoryStats)
    .sort(([, countA], [, countB]) => countB - countA);

  return (
    <div className="page">
      <div className="page-header">
        <div className="header-content">
          <h1> </h1>
          <p>   </p>
        </div>
        <Link to="/technologies" className="btn btn-secondary">
             
        </Link>
      </div>

      {/*  */}
      <div className="settings-section">
        <h2>  </h2>
        <div className="stats-grid">
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{technologies.length}</div>
              <div className="stat-label"> </div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">
                {technologies.filter(t => t.status === 'completed').length}
              </div>
              <div className="stat-label"></div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">
                {technologies.filter(t => t.status === 'in-progress').length}
              </div>
              <div className="stat-label"> </div>
            </div>
          </div>
          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{Object.keys(categoryStats).length}</div>
              <div className="stat-label"></div>
            </div>
          </div>
        </div>

        {/*    */}
        {sortedCategories.length > 0 && (
          <div className="category-stats">
            <h4>   :</h4>
            <div className="category-bars">
              {sortedCategories.map(([category, count]) => (
                <div key={category} className="category-bar">
                  <div className="category-info">
                    <div className="category-name-wrapper">
                      <span className="category-icon">
                        {getCategoryIcon(category)}
                      </span>
                      <span className="category-name">
                        {getCategoryName(category)}
                      </span>
                    </div>
                    <span className="category-count">{count}</span>
                  </div>
                  <div className="bar-container">
                    <div 
                      className="bar-fill"
                      style={{ 
                        width: `${(count / technologies.length) * 100}%` 
                      }}
                      title={`${count}  (${Math.round((count / technologies.length) * 100)}%)`}
                    ></div>
                  </div>
                  <div className="category-percentage">
                    {Math.round((count / technologies.length) * 100)}%
                  </div>
                </div>
              ))}
            </div>
          </div>
        )}
      </div>

      {/*   */}
      <div className="settings-section">
        <h2>  </h2>
        <p>    </p>
        
        <div className="action-cards">
          <div className="action-card">
            <div className="action-icon"></div>
            <div className="action-content">
              <h4>   </h4>
              <p>  ""   </p>
            </div>
            <button 
              onClick={handleMarkAllCompleted}
              className="btn btn-primary"
              disabled={technologies.length === 0}
            >
              
            </button>
          </div>

          <div className="action-card">
            <div className="action-icon"></div>
            <div className="action-content">
              <h4>  </h4>
              <p>     " "</p>
            </div>
            <button 
              onClick={() => setShowResetConfirm(true)}
              className="btn btn-secondary"
              disabled={technologies.length === 0}
            >
              
            </button>
          </div>
        </div>
      </div>

      {/*   */}
      <div className="settings-section">
        <h2>  </h2>
        <p>         </p>
        <DataExporter technologies={technologies} />
      </div>

      {/*   */}
      <div className="settings-section">
        <h2>  </h2>
        <p>    </p>
        <DataImporter 
          onImport={handleImport}
          existingTechnologies={technologies}
        />
      </div>

      {/*   */}
      <div className="settings-section danger-zone">
        <h2>  </h2>
        <p>,   </p>
        
        <div className="action-cards">
          <div className="action-card danger">
            <div className="action-icon"></div>
            <div className="action-content">
              <h4>  </h4>
              <p>   .    !</p>
            </div>
            <button 
              onClick={() => setShowDeleteAllConfirm(true)}
              className="btn btn-danger"
              disabled={technologies.length === 0}
            >
               
            </button>
          </div>
        </div>
      </div>

      {/*    */}
      {showResetConfirm && (
        <div className="modal-overlay">
          <div className="modal">
            <div className="modal-header">
              <h3>   ?</h3>
            </div>
            <div className="modal-content">
              <p>    " "   {technologies.length} .</p>
              <p> ,   ?</p>
            </div>
            <div className="modal-actions">
              <button 
                onClick={() => setShowResetConfirm(false)}
                className="btn btn-secondary"
              >
                
              </button>
              <button 
                onClick={handleResetAllStatuses}
                className="btn btn-primary"
              >
                , 
              </button>
            </div>
          </div>
        </div>
      )}

      {showDeleteAllConfirm && (
        <div className="modal-overlay">
          <div className="modal danger">
            <div className="modal-header">
              <h3>   ?</h3>
            </div>
            <div className="modal-content">
              <p>    {technologies.length}    .</p>
              <p><strong>   !</strong></p>
              <p> ,   ?</p>
            </div>
            <div className="modal-actions">
              <button 
                onClick={() => setShowDeleteAllConfirm(false)}
                className="btn btn-secondary"
              >
                
              </button>
              <button 
                onClick={handleDeleteAll}
                className="btn btn-danger"
              >
                ,  
              </button>
            </div>
          </div>
        </div>
      )}
    </div>
  );
}

export default Settings;