// components/Navigation.jsx
import React, { useState } from 'react';
import { Link, useLocation } from 'react-router-dom';
import './Navigation.css';

function Navigation() {
  const [isMobileMenuOpen, setIsMobileMenuOpen] = useState(false);
  const location = useLocation();

  const toggleMobileMenu = () => {
    setIsMobileMenuOpen(!isMobileMenuOpen);
  };

  const closeMobileMenu = () => {
    setIsMobileMenuOpen(false);
  };

  const isActiveLink = (path) => {
    return location.pathname === path;
  };

  return (
    <nav className="navigation">
      <div className="nav-container">
        {/*    */}
        <div className="nav-brand">
          <Link to="/" className="brand-link" onClick={closeMobileMenu}>
            <span className="brand-icon"></span>
            <span className="brand-text">TechTracker</span>
          </Link>
        </div>

        {/*    */}
        <button 
          className={`mobile-menu-btn ${isMobileMenuOpen ? 'active' : ''}`}
          onClick={toggleMobileMenu}
          aria-label=" "
        >
          <span></span>
          <span></span>
          <span></span>
        </button>

        {/*   */}
        <div className={`nav-links ${isMobileMenuOpen ? 'active' : ''}`}>
          <Link 
            to="/" 
            className={`nav-link ${isActiveLink('/') ? 'active' : ''}`}
            onClick={closeMobileMenu}
          >
            <span className="nav-icon"></span>
            <span className="nav-text"></span>
          </Link>

          <Link 
            to="/technologies" 
            className={`nav-link ${isActiveLink('/technologies') ? 'active' : ''}`}
            onClick={closeMobileMenu}
          >
            <span className="nav-icon"></span>
            <span className="nav-text"></span>
          </Link>

          <Link 
            to="/add-technology" 
            className={`nav-link ${isActiveLink('/add-technology') ? 'active' : ''}`}
            onClick={closeMobileMenu}
          >
            <span className="nav-icon"></span>
            <span className="nav-text"></span>
          </Link>

          <Link 
            to="/statistics" 
            className={`nav-link ${isActiveLink('/statistics') ? 'active' : ''}`}
            onClick={closeMobileMenu}
          >
            <span className="nav-icon"></span>
            <span className="nav-text"></span>
          </Link>

          <Link 
            to="/settings" 
            className={`nav-link ${isActiveLink('/settings') ? 'active' : ''}`}
            onClick={closeMobileMenu}
          >
            <span className="nav-icon"></span>
            <span className="nav-text"></span>
          </Link>
        </div>

        {/*       */}
        <div className="current-page-mobile">
          {isActiveLink('/') && ''}
          {isActiveLink('/technologies') && ''}
          {isActiveLink('/add-technology') && ' '}
          {isActiveLink('/statistics') && ''}
          {isActiveLink('/settings') && ''}
        </div>
      </div>

      {/* Overlay    */}
      {isMobileMenuOpen && (
        <div className="mobile-overlay" onClick={closeMobileMenu}></div>
      )}
    </nav>
  );
}

export default Navigation;