// pages/Home.jsx
import React from 'react';
import { Link } from 'react-router-dom';
import useTechnologiesApi from '../hooks/useTechnologiesApi';
import DailyQuote from '../components/DailyQuote';
import './Home.css';

function Home() {
  const { 
    technologies, 
    progress, 
    completedCount, 
    inProgressCount,
    dailyQuote,
    quoteLoading,
    fetchDailyQuote
  } = useTechnologiesApi();

  //    
  const recentTechnologies = technologies
    .sort((a, b) => new Date(b.importedAt || b.id) - new Date(a.importedAt || a.id))
    .slice(0, 3);

  //     
  const inProgressTechs = technologies
    .filter(tech => tech.status === 'in-progress')
    .slice(0, 3);

  //      
  const getCategoryStats = () => {
    const stats = {};
    technologies.forEach(tech => {
      if (!stats[tech.category]) {
        stats[tech.category] = 0;
      }
      stats[tech.category]++;
    });
    return stats;
  };

  const categoryStats = getCategoryStats();
  const topCategories = Object.entries(categoryStats)
    .sort(([,a], [,b]) => b - a)
    .slice(0, 3);

  const getCategoryIcon = (category) => {
    const icons = {
      'frontend': '',
      'backend': '',
      'database': '',
      'devops': '',
      'mobile': '',
      'ai-ml': '',
      'cloud': '',
      'tools': '',
      'language': '',
      'other': ''
    };
    return icons[category] || '';
  };

  const getCategoryName = (category) => {
    const names = {
      'frontend': 'Frontend',
      'backend': 'Backend',
      'database': ' ',
      'devops': 'DevOps',
      'mobile': ' ',
      'ai-ml': 'AI/ML',
      'cloud': ' ',
      'tools': '',
      'language': ' ',
      'other': ''
    };
    return names[category] || category;
  };

  const getStatusIcon = (status) => {
    switch (status) {
      case 'completed': return '';
      case 'in-progress': return '';
      default: return '';
    }
  };

  const getStatusText = (status) => {
    switch (status) {
      case 'completed': return '';
      case 'in-progress': return ' ';
      default: return ' ';
    }
  };

  return (
    <div className="page">
      {/*  */}
      <div className="page-header">
        <div className="header-content">
          <h1>    TechTracker</h1>
          <p>          </p>
        </div>
        <div className="header-actions">
          <Link to="/add-technology" className="btn btn-primary">
              
          </Link>
        </div>
      </div>

      {/*   */}
      <DailyQuote 
        quote={dailyQuote}
        loading={quoteLoading}
        onRefresh={fetchDailyQuote}
      />

      {/*   */}
      <div className="stats-section">
        <h2>  </h2>
        <div className="stats-grid">
          <div className="stat-card main-stat">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{progress}%</div>
              <div className="stat-label"> </div>
              <div className="progress-bar">
                <div 
                  className="progress-fill"
                  style={{ width: `${progress}%` }}
                ></div>
              </div>
            </div>
          </div>

          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{completedCount}</div>
              <div className="stat-label"></div>
              <div className="stat-subtext"></div>
            </div>
          </div>

          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{inProgressCount}</div>
              <div className="stat-label"> </div>
              <div className="stat-subtext"></div>
            </div>
          </div>

          <div className="stat-card">
            <div className="stat-icon"></div>
            <div className="stat-content">
              <div className="stat-value">{technologies.length}</div>
              <div className="stat-label"></div>
              <div className="stat-subtext"> </div>
            </div>
          </div>
        </div>
      </div>

      <div className="content-grid">
        {/*     */}
        <div className="content-section">
          <div className="section-header">
            <h2>   </h2>
            <Link to="/technologies" className="view-all-link">
                
            </Link>
          </div>
          
          {inProgressTechs.length > 0 ? (
            <div className="tech-cards">
              {inProgressTechs.map(tech => (
                <div key={tech.id} className="tech-card">
                  <div className="tech-header">
                    <h3>{tech.title}</h3>
                    <span className="status-badge in-progress">
                      {getStatusIcon(tech.status)} {getStatusText(tech.status)}
                    </span>
                  </div>
                  <p className="tech-description">{tech.description}</p>
                  <div className="tech-meta">
                    <span className="category">
                      {getCategoryIcon(tech.category)} {getCategoryName(tech.category)}
                    </span>
                    {tech.estimatedHours && (
                      <span className="hours"> {tech.estimatedHours}</span>
                    )}
                  </div>
                  <Link 
                    to={`/technology/${tech.id}`}
                    className="btn btn-secondary btn-small"
                  >
                    
                  </Link>
                </div>
              ))}
            </div>
          ) : (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h4>  </h4>
              <p>  -     </p>
              <Link to="/technologies" className="btn btn-primary">
                  
              </Link>
            </div>
          )}
        </div>

        {/*   */}
        <div className="content-section">
          <div className="section-header">
            <h2>  </h2>
            <Link to="/add-technology" className="view-all-link">
                
            </Link>
          </div>
          
          {recentTechnologies.length > 0 ? (
            <div className="tech-cards">
              {recentTechnologies.map(tech => (
                <div key={tech.id} className="tech-card">
                  <div className="tech-header">
                    <h3>{tech.title}</h3>
                    <span className={`status-badge ${tech.status}`}>
                      {getStatusIcon(tech.status)} {getStatusText(tech.status)}
                    </span>
                  </div>
                  <p className="tech-description">{tech.description}</p>
                  <div className="tech-meta">
                    <span className="category">
                      {getCategoryIcon(tech.category)} {getCategoryName(tech.category)}
                    </span>
                    <span className="difficulty">
                      {tech.difficulty === 'beginner' && ''}
                      {tech.difficulty === 'intermediate' && ''}
                      {tech.difficulty === 'advanced' && ''}
                    </span>
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h4>  </h4>
              <p>      </p>
              <Link to="/add-technology" className="btn btn-primary">
                 
              </Link>
            </div>
          )}
        </div>

        {/*   */}
        <div className="content-section">
          <div className="section-header">
            <h2>  </h2>
            <Link to="/statistics" className="view-all-link">
                
            </Link>
          </div>
          
          {topCategories.length > 0 ? (
            <div className="category-cards">
              {topCategories.map(([category, count]) => (
                <div key={category} className="category-card">
                  <div className="category-icon">
                    {getCategoryIcon(category)}
                  </div>
                  <div className="category-content">
                    <h4>{getCategoryName(category)}</h4>
                    <p>{count} </p>
                  </div>
                  <div className="category-percentage">
                    {Math.round((count / technologies.length) * 100)}%
                  </div>
                </div>
              ))}
            </div>
          ) : (
            <div className="empty-state">
              <div className="empty-icon"></div>
              <h4>   </h4>
              <p>      </p>
            </div>
          )}
        </div>

        {/*   */}
        <div className="content-section">
          <div className="section-header">
            <h2>  </h2>
          </div>
          <div className="quick-actions">
            <Link to="/add-technology" className="quick-action-card">
              <div className="action-icon"></div>
              <div className="action-content">
                <h4> </h4>
                <p>   </p>
              </div>
            </Link>

            <Link to="/technologies" className="quick-action-card">
              <div className="action-icon"></div>
              <div className="action-content">
                <h4> </h4>
                <p>    </p>
              </div>
            </Link>

            <Link to="/statistics" className="quick-action-card">
              <div className="action-icon"></div>
              <div className="action-content">
                <h4></h4>
                <p>   </p>
              </div>
            </Link>

            <Link to="/settings" className="quick-action-card">
              <div className="action-icon"></div>
              <div className="action-content">
                <h4></h4>
                <p>   </p>
              </div>
            </Link>
          </div>
        </div>
      </div>
    </div>
  );
}

export default Home;