// pages/AddTechnology.jsx
import React, { useState } from 'react';
import { Link } from 'react-router-dom';
import TechnologySearch from '../components/TechnologySearch';
import useTechnologiesApi from '../hooks/useTechnologiesApi';
import './AddTechnology.css';

function AddTechnology() {
  const { 
    addTechnology, 
    searchTechnologies, 
    searchResults, 
    searchLoading, 
    importTechnology
  } = useTechnologiesApi();

  const [formData, setFormData] = useState({
    title: '',
    description: '',
    category: 'frontend',
    difficulty: 'beginner',
    estimatedHours: '',
    resources: ''
  });

  const [showSuccessMessage, setShowSuccessMessage] = useState(false);

  const handleInputChange = (e) => {
    const { name, value } = e.target;
    setFormData(prev => ({
      ...prev,
      [name]: value
    }));
  };

  const handleSubmit = (e) => {
    e.preventDefault();
    
    if (!formData.title.trim()) {
      alert(',   ');
      return;
    }

    const techData = {
      title: formData.title.trim(),
      description: formData.description.trim(),
      category: formData.category,
      difficulty: formData.difficulty,
      estimatedHours: formData.estimatedHours ? parseInt(formData.estimatedHours) : undefined,
      resources: formData.resources 
        ? formData.resources.split('\n').filter(url => url.trim())
        : []
    };

    addTechnology(techData);
    
    //    
    setShowSuccessMessage(true);
    
    //  
    setFormData({
      title: '',
      description: '',
      category: 'frontend',
      difficulty: 'beginner',
      estimatedHours: '',
      resources: ''
    });

    //    3 
    setTimeout(() => {
      setShowSuccessMessage(false);
    }, 3000);
  };

  const categories = [
    { value: 'frontend', label: ' Frontend' },
    { value: 'backend', label: ' Backend' },
    { value: 'database', label: ' Database' },
    { value: 'devops', label: ' DevOps' },
    { value: 'mobile', label: ' Mobile' },
    { value: 'ai-ml', label: ' AI/ML' },
    { value: 'cloud', label: ' Cloud' },
    { value: 'tools', label: ' Tools' },
    { value: 'language', label: ' Language' },
    { value: 'other', label: ' Other' }
  ];

  const difficultyLevels = [
    { value: 'beginner', label: ' ' },
    { value: 'intermediate', label: ' ' },
    { value: 'advanced', label: ' ' }
  ];

  return (
    <div className="page">
      <div className="page-header">
        <div className="header-content">
          <h1> </h1>
          <p>       </p>
        </div>
        <Link to="/technologies" className="btn btn-secondary">
             
        </Link>
      </div>

      {/*    */}
      {showSuccessMessage && (
        <div className="success-message">
          <span>   !</span>
        </div>
      )}

      {/*      API */}
      <TechnologySearch 
        onSearch={searchTechnologies}
        searchResults={searchResults}
        searchLoading={searchLoading}
        onImport={importTechnology}
      />

      {/*    */}
      <div className="card manual-form-card">
        <div className="card-header">
          <h2>   </h2>
          <p>     </p>
        </div>
        
        <form onSubmit={handleSubmit} className="tech-form">
          <div className="form-group">
            <label htmlFor="title" className="form-label">
                *
            </label>
            <input
              type="text"
              id="title"
              name="title"
              value={formData.title}
              onChange={handleInputChange}
              placeholder=": React, Docker, MongoDB..."
              className="form-input"
              required
            />
          </div>

          <div className="form-group">
            <label htmlFor="description" className="form-label">
              
            </label>
            <textarea
              id="description"
              name="description"
              value={formData.description}
              onChange={handleInputChange}
              placeholder="  ,       ..."
              className="form-textarea"
              rows="3"
            />
          </div>

          <div className="form-row">
            <div className="form-group">
              <label htmlFor="category" className="form-label">
                
              </label>
              <select
                id="category"
                name="category"
                value={formData.category}
                onChange={handleInputChange}
                className="form-select"
              >
                {categories.map(cat => (
                  <option key={cat.value} value={cat.value}>
                    {cat.label}
                  </option>
                ))}
              </select>
            </div>

            <div className="form-group">
              <label htmlFor="difficulty" className="form-label">
                
              </label>
              <select
                id="difficulty"
                name="difficulty"
                value={formData.difficulty}
                onChange={handleInputChange}
                className="form-select"
              >
                {difficultyLevels.map(level => (
                  <option key={level.value} value={level.value}>
                    {level.label}
                  </option>
                ))}
              </select>
            </div>
          </div>

          <div className="form-group">
            <label htmlFor="estimatedHours" className="form-label">
                 ()
            </label>
            <input
              type="number"
              id="estimatedHours"
              name="estimatedHours"
              value={formData.estimatedHours}
              onChange={handleInputChange}
              placeholder=": 40"
              min="1"
              max="1000"
              className="form-input"
            />
            <small className="form-hint">
               ,     
            </small>
          </div>

          <div className="form-group">
            <label htmlFor="resources" className="form-label">
                
            </label>
            <textarea
              id="resources"
              name="resources"
              value={formData.resources}
              onChange={handleInputChange}
              placeholder="    (   ):&#10;https://react.dev&#10;https://ru.reactjs.org"
              className="form-textarea"
              rows="4"
            />
            <small className="form-hint">
                    
            </small>
          </div>

          <div className="form-actions">
            <button type="submit" className="btn btn-primary btn-large">
                
            </button>
            <button 
              type="button" 
              onClick={() => setFormData({
                title: '',
                description: '',
                category: 'frontend',
                difficulty: 'beginner',
                estimatedHours: '',
                resources: ''
              })}
              className="btn btn-secondary"
            >
                
            </button>
          </div>
        </form>
      </div>

      {/*  */}
      <div className="card tips-card">
        <div className="card-header">
          <h3>    </h3>
        </div>
        <div className="tips-content">
          <div className="tip-item">
            <strong> </strong>
            <p>      -          .</p>
          </div>
          <div className="tip-item">
            <strong>  </strong>
            <p>        .</p>
          </div>
          <div className="tip-item">
            <strong> </strong>
            <p>   ,      .</p>
          </div>
          <div className="tip-item">
            <strong>  </strong>
            <p>         .</p>
          </div>
        </div>
      </div>
    </div>
  );
}

export default AddTechnology;